const getTargets = (excludeList = []) => {
  return Object.values(langCodes).filter((langCode) => {
    return (
      langCode.id !== langCodes.NONE.id &&
      !excludeList.some((excludeItem) => excludeItem.id === langCode.id)
    );
  });
};

